import itemList from "component/item-list.vue"
export default {
	components: {
		itemList
	},
	data() {
		return {
			/* 轮播图 */
			swiperList: [{}, {}],
			/* 商家列表 */
			historyList: [{
					oneDayList: [{}, {}, {}]
				}, {
					oneDayList: [{}, {}, {}, {}, {}]
				},
				{
					oneDayList: [{}]
				}
			]
		}
	},
	// onReachBottom() {
	// 	uni.showLoading({
	// 		title: '加载中',
	// 		mask: true
	// 	})
	// 	setTimeout(function() {
	// 		uni.hideLoading()
	// 	}, 1000);
	// },
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		/* 批量编辑 */
		clearAll() {
			let _this = this
			uni.showModal({
				title: '确认要清空足迹吗？',
				content: '清空后将无法恢复',
				success: res => {
					if (res.confirm) {
						_this.historyList = []
						uni.showToast({
							title: '操作成功',
							icon: 'none'
						});
						uni.pageScrollTo({
							scrollTop: 0,
						});
					}
				}
			});
		},
		/* 单个编辑 */
		clearHistory(item, index, item1, index1) {
			this.historyList[index].oneDayList.splice(index1, 1)
			uni.showToast({
				title: '删除成功',
				icon: 'none',
			});
		},
		/* 页面跳转 */
		jump2home() {
			uni.reLaunch({
				url: '/pages/index/index',
			})
		},
		jump2item(item, index) {
			uni.navigateTo({
				url: '/pages/item/index'
			})
		},
	}
}
